<template>
  <div class="content-page">
    <div>
      <input id="csv_file" ref="csv_file" type="file" @change="uploadCsv()" />
    </div>

    <div class="quiz-container">
      <div class="header-container">
        <button class="cus-btn" @click="chooseFile()">
          <i class="fa fa-cloud-upload" aria-hidden="true"></i>
        </button>
        <span>{{ title }}</span>
        <button class="cus-btn" @click="saveData()">Save</button>
      </div>
      <hr />
      <div class="data-render-container">
        <table>
          <thead>
            <tr>
              <th scope="col" style="width: 50px">#</th>
              <th scope="col">Ja</th>
              <th scope="col">Vi</th>
            </tr>
          </thead>
          <tbody>
            <RowComponent
              v-for="(question, index) in datas.questions"
              :key="index"
              :question="question"
              :no-index="index"
              :update-question="
                (data, noIndex) => updateQuestion(data, noIndex)
              "
              :loaded="loaded"
              type-table="upload"
            />
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import FirestoreService from "@/services/firestore";
import { QUIZ_TYPE } from "@/definition/constants";
import kuromoji from "kuromoji/src/kuromoji.js";
import { toHiragana, toRomaji } from "wanakana";
import RowComponent from "@/components/RowComponent.vue";
import Papa from "papaparse";

export default {
  components: {
    RowComponent,
  },
  data() {
    return {
      tokenizerConvert: null,
      datas: [],
      loaded: false,
      quiz: null,
      title: "",
    };
  },
  beforeCreate() {
    setTimeout(() => {
      kuromoji
        .builder({ dicPath: "../assets/dist/" })
        .build((err, tokenizer) => {
          if (err) {
            alert("error kuromoji");
          }
          this.log(tokenizer);
          this.tokenizerConvert = tokenizer;
        });
    }, 100);
  },
  methods: {
    async uploadCsv() {
      this.datas = [];
      this.loaded = false;
      const csv_file = document.getElementById("csv_file");
      const file = csv_file.files[0];
      const dataCsv = await this.parstCsv(file);

      this.title = file.name;

      dataCsv.forEach((element) => {
        if (!element.text_jp) {
          element.text_jp = this.convertToHiragana(element.text_kanji);
        }
        element.noun_hira = this.convertToHiragana(element.noun);
        element.romanji = toRomaji(element.text_jp);
        element.select = true;
        element.text_vi = this.capitalizeFirstLetter(element.text_vi);
      });

      this.datas = {
        title: file.name,
        del_flag: false,
        questions: dataCsv,
        created_at: new Date().getTime(),
        type: file.name.startsWith(QUIZ_TYPE.KANJI) ? 2 : 1,
      };

      setTimeout(() => {
        this.loaded = true;
      }, 300);
    },
    convertToHiragana(textKanji) {
      const tokens = this.tokenizerConvert.tokenize(textKanji);

      return tokens
        .map((token) =>
          token.reading ? toHiragana(token.reading) : token.surface_form
        )
        .join("");
    },
    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    chooseFile() {
      this.$refs.csv_file.click();
    },
    updateQuestion(data, noIndex) {
      if (this.loaded) {
        this.datas.questions[noIndex] = data;
      }
    },
    async saveData() {
      const quiz = await FirestoreService.getQuizWithFileName(
        "quizs",
        this.datas.title
      );

      if (quiz.length > 0) {
        this.datas.created_at = quiz[0].created_at;
        FirestoreService.set("quizs", quiz[0].id, this.datas).then(() => {
          alert("Updated");
        });
      } else {
        FirestoreService.create("quizs", this.datas).then(() => {
          alert("Success");
        });
      }
    },
    async parstCsv(file) {
      return new Promise((resolve, reject) => {
        Papa.parse(file, {
          header: true,
          skipEmptyLines: true,
          complete: function (results) {
            resolve(results.data);
          }.bind(this),
        });
      });
    },
  },
};
</script>
