<template>
  <tr :class="{ 'select-question': data.select }">
    <td rowspan="2" class="cpointer cant-select" style="text-align: center">
      <input
        :id="`question_${noIndex}`"
        v-model="data.select"
        class="d-none"
        type="checkbox"
        @change="toggleQuestionItem()"
      />
      <label class="lib-question cpointer" :for="`question_${noIndex}`">{{
        noIndex + 1
      }}</label>
    </td>
    <td>
      <div style="display: flex; white-space: nowrap">
        <span v-if="data.noun_hira" style="margin-right: 8px">{{
          data.noun_hira
        }}</span>
        <span v-if="data.noun" style="margin-right: 8px">{{ data.sub }}</span>
        <input
          v-model="data.text_jp"
          :class="{ 'cl-fff': hideText }"
          type="text"
          @change="handleChange()"
        />
      </div>
    </td>
    <td v-if="typeTable === 'quiz'" rowspan="2">
      <label class="lib-play" @click="textToSpeech(data)">
        <i class="fa fa-volume-up" aria-hidden="true"></i>
      </label>
    </td>
    <td rowspan="2">
      <textarea
        id=""
        v-model="data.text_vi"
        name=""
        rows="2"
        @change="handleChange()"
      ></textarea>
    </td>
  </tr>
  <tr :class="{ 'select-question': data.select }">
    <td>
      <div style="display: flex; white-space: nowrap">
        <span v-if="data.noun" style="margin-right: 8px">{{ data.noun }}</span>
        <span v-if="data.noun" style="margin-right: 8px">{{ data.sub }}</span>
        <input
          v-model="data.text_kanji"
          :class="{ 'cl-fff': hideText }"
          type="text"
          @change="handleChange()"
        />
      </div>
    </td>
  </tr>
</template>

<script>
export default {
  components: {},
  props: [
    "question",
    "noIndex",
    "updateQuestion",
    "loaded",
    "toggleQuestion",
    "toggleAll",
    "typeTable",
    "textToSpeech",
    "hideText",
  ],
  data() {
    return {
      data: this.question,
    };
  },
  watch: {
    question(newVal) {
      this.data = newVal;
    },
  },
  methods: {
    handleChange() {
      if (this.loaded) {
        this.updateQuestion(this.data, this.noIndex);
      }
    },
    toggleQuestionItem() {
      if (this.toggleQuestion) {
        this.toggleQuestion(this.noIndex);
      }
    },
  },
};
</script>

